  formatDate(item) {

    let separar = item.split(':');
    let first = separar[0]
    let hora = '';

    if (first.length == 1) {
      hora = '0' + first + ':' + separar[1]
    } else if (first.length > 1) {
      hora = first + ':' + separar[1]
    }

    return hora;

  }

  addBreak(title, arr, variable, variable2, dia) {

    moment.locale('es')

    let desde = arr[0].Values.filter((it) => it[variable]).length > 0 ? arr[0].Values.filter((it) => it[variable])[0][variable] : '';
    let hasta = arr[0].Values.filter((it) => it[variable2]).length > 0 ? arr[0].Values.filter((it) => it[variable2])[0][variable2] : '';

    if (desde && hasta) {

      let diff = moment(this.to).diff(moment(this.from), 'days');
      let horaDesde = this.formatDate(desde);
      let horaHasta = this.formatDate(hasta);
      

      for (let i = 0; i < diff; i++) {
        let fecha = moment(this.from).format('YYYY-MM-DD');
        console.log(i)
        if (i >= 1) {
          fecha = moment(this.from).add(i, 'days').format('YYYY-MM-DD');
        }

        var diaName = moment(fecha).format('dddd')

        if (diaName == dia) {

          console.log(diaName)
          let generalDesde = moment(fecha + ' ' + horaDesde).format('YYYY-MM-DD HH:mm')
          let generalHasta = moment(fecha + ' ' + horaHasta).format('YYYY-MM-DD HH:mm')
  
          console.log(generalDesde)
  
          this.calendarRef.nativeElement.getApi().addEvent({
            title: title,
            start:generalDesde,
            end:  generalHasta,
            id: 'BREAK',
            editable:false,
            allDay: false,
          //  display: 'background',
            backgroundColor: '#e1e1e1',
            color: '#000000',
            textColor: '#000000'
  
  
            
          }) 

        }

     

        
      }

    }
         
  }

